body{
    background-image: url(../../Imagenes/pizza-fondo.jpg);
    background-attachment: fixed;
}


.mostrar_mesa{
    /* height: 500px; */
    display: grid;
    justify-content: center;
}
.ingresar_la_mesa{
    margin-top: 150px;
}
.boton_ingresar_la_mesa{
    height: 50px;
}

.no_mostrar_pedido, .no_mostrar_mesa{
 opacity: 0;
 width: 0;
 height: 0;
}

.boton_nuevo_inicio {
    /* position: fixed ; */
    font-family: 'Akaya Telivigala', cursive;
	margin-top: 5px;
    margin-left: 5px;
    border-radius: 0px 25px 25px ;
    background: linear-gradient(to left, #53C7E3 , #A1E3F3); 
	border-color: #A1E3F3;

	outline: none;
	padding: 3px 10px;
	color: white;
	font-size: 18px;
}

